export interface TrueEndingState {
  isActive: boolean; // 
  currentPhase: number; //  (0-5)
  turnsInPhase: number; // 
  completedAnchors: string[]; // 
  totalTurns: number; // 
  isComplete: boolean; // 
  // Bug #15 
  noProgressTurns: number; // 
  hintGiven: boolean; // 
}

/**
 * 
 */
export function getDefaultTrueEndingState(): TrueEndingState {
  return {
    isActive: false,
    currentPhase: 0,
    turnsInPhase: 0,
    completedAnchors: [],
    totalTurns: 0,
    isComplete: false,
    noProgressTurns: 0,
    hintGiven: false,
  };
}

