export interface PerfectEndingState {
  isActive: boolean; // 
  currentPhase: number; //  (0-11)
  turnsInPhase: number; // 
  completedAnchors: string[]; // 
  totalTurns: number; // 
  isComplete: boolean; // 
  // Bug #15 
  noProgressTurns: number; // 
  hintGiven: boolean; // 
}

/**
 * 
 */
export function getDefaultPerfectEndingState(): PerfectEndingState {
  return {
    isActive: false,
    currentPhase: 0,
    turnsInPhase: 0,
    completedAnchors: [],
    totalTurns: 0,
    isComplete: false,
    // Bug #15 
    noProgressTurns: 0,
    hintGiven: false,
  };
}

