/**
 * AIPrompt
 * Bug #19: 
 * Bug #40 ""
 * @param state 
 * @param userInput 
 * @param currentHour 
 */
export function generatePerfectEndingPrompt(
  state: PerfectEndingState,
  userInput: string,
  currentHour?: number,
): string {
  const phaseConfig = PERFECT_ENDING_PHASES[state.currentPhase];
  if (!phaseConfig) return '';

  // 
  const remainingAnchors = phaseConfig.anchorEvents.filter(a => !state.completedAnchors.includes(a));

  // Bug #40 
  const hour = currentHour ?? 12;
  const nextPhaseCheck = canEnterNextPerfectPhase(state, hour);
  const isTimeLocked = nextPhaseCheck.blocked;

  // Bug #40 ""
  let turnsWarning: string;
  if (isTimeLocked) {
    turnsWarning = `${PERFECT_ENDING_PHASES[state.currentPhase + 1]?.minHour}:00`;
  } else {
    const remainingTurns = phaseConfig.maxTurns - state.turnsInPhase;
    turnsWarning =
      remainingTurns <= 2 ? ` ${remainingTurns}` : `${remainingTurns}`;
  }

  // Bug #40 
  let urgencyHint = '';
  if (!isTimeLocked) {
    const remainingTurns = phaseConfig.maxTurns - state.turnsInPhase;
    if (remainingTurns <= 1) {
      urgencyHint = '\n\n 1';
    } else if (remainingTurns <= 2) {
      urgencyHint = `\n\n ${remainingTurns} `;
    }
  }

  // Bug #15 
  let hintSection = '';
  if (phaseConfig.progressHint && state.noProgressTurns >= (phaseConfig.hintThreshold ?? 2) && !state.hintGiven) {
    hintSection = `\n\n  - 

"${phaseConfig.progressHint}"


- 
- /
- `;
  }

  // Bug #40 
  const timeBlockWarning = isTimeLocked
    ? `\n "${PERFECT_ENDING_PHASES[state.currentPhase + 1]?.name}"${PERFECT_ENDING_PHASES[state.currentPhase + 1]?.minHour}:00${hour}:00
 `
    : '';

  // 
  const progressInfo = `
- ${state.currentPhase + 1}/${PERFECT_ENDING_PHASES.length}${phaseConfig.name}
- ${state.turnsInPhase}${turnsWarning}
- ${remainingAnchors.length > 0 ? remainingAnchors.join('') : ''}
- ${state.totalTurns}${timeBlockWarning}`;

  // Prompt
  return `${phaseConfig.aiGuidance}

${progressInfo}${urgencyHint}${hintSection}


- 
- 
- 
- """"
- 
- `;
}

/**
 * 
 */
export function getPerfectCorrectionHint(phase: number): string | null {
  const phaseConfig = PERFECT_ENDING_PHASES[phase];
  if (!phaseConfig || phaseConfig.correctionHints.length === 0) {
    return null;
  }

  // 
  const index = Math.floor(Math.random() * phaseConfig.correctionHints.length);
  return phaseConfig.correctionHints[index];
}

