/**
 * 
 *   false  true true  false
 */
export function validateTruthModeTransition(oldValue: boolean, newValue: boolean): boolean {
  if (oldValue === true && newValue === false) {
    console.error('[] : truefalse');
    return false;
  }
  return true;
}

/**
 * 
 * AI
 *
 * @param path 
 * @param oldValue 
 * @param newValue 
 * @param maxChange 
 */
export function validateNumericChange(
  path: string,
  oldValue: number,
  newValue: number,
  maxChange: number = 10,
): boolean {
  const change = Math.abs(newValue - oldValue);

  if (change > maxChange) {
    console.warn(
      `[] : ${path}  ${oldValue}  ${newValue}: ${change}: ${maxChange}`,
    );
    return false;
  }

  return true;
}

