/**
 * 
 */
function randomChoice<T>(arr: T[]): T {
  return arr[Math.floor(Math.random() * arr.length)];
}

/**
 * 
 * @param day 1-5
 * @param hour 0-23
 * @returns 
 */
function calculateGlobalHour(day: number, hour: number): number {
  return (day - 1) * 24 + hour;
}

